<template>
<transition name="slide">
  <music-list :songs='song' 
              :bgImage='bigImage' 
              :title="title"
              ></music-list>
</transition> 
</template>
<script type='text/ecmascript-6'>
import {mapGetters} from 'vuex'
import {getSingerDetail} from 'api/singer'
import {ERR_OK} from 'api/config'
import {createSong} from 'common/js/song'
import MusicList from 'components/music-list/music-list'
  export default{
    data(){
      return {
        song: []
      }
    },
    computed:{
      title(){
        return this.singer.name
      },
      bigImage(){
        return this.singer.avatar
      },
      ...mapGetters(['singer']),//将singer这个数据传入到当前模块
    },
    created(){
      this._getDetail()
    },
    methods:{
      _getDetail(){
        if(!this.singer.id){//若当前singerid获取失败则返回歌手页面
          console.log(3)
          this.$router.push('/singer')
          return
        }
        getSingerDetail(this.singer.id).then((res)=>{//根据singer.id来获取当前的数据
          if(res.code == ERR_OK){
            this.song = this._normallizeSongs(res.data.list)
          }
        })
      },
    _normallizeSongs(list){//返回一个数组 对象
      let ret = []
      list.forEach((item)=>{
        let musicData = item.musicData
        if(musicData.songid && musicData.albummid){
          ret.push(createSong(musicData))//createSong在common 中封装的一个方法 根据传入的数据返回一个新的对象
        }
      })
      return ret
    }
  },
  components:{
    MusicList
  }
  }
</script>

<style scoped lang='stylus' rel='stylesheet/stylus'>
  @import "~common/stylus/variable" ;

    .slide-enter-active,slide-leave-activated
        transition : all 0.3s
    .slide-enter,.slide-leave-to
      transform: translate3d(100%,0,0)

</style>


