import jsonp from 'common/js/jsonp' /*jsonp返回的是一个promise*/
import {commonParams,options} from './config'/*options回调函数的名字*/

export function getSingerList(){
  const url='https://c.y.qq.com/v8/fcg-bin/v8.fcg'
  const data = Object.assign({},commonParams,{
    channel:'singer',
    page:'list',
    key:'all_all_all',
    pagesize:100,
    pagenum:1,
    hostUin:0,
    needNewCode:0,
    g_tk:5381,
    platform:'yqq'
  })

  return jsonp(url,data,options)
}

export function getSingerDetail(SingerId){
  const url = 'https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg'
  const data = Object.assign({},commonParams,{
    order:'listen',
    begin:0,
    num:100,
    singermid:SingerId,
    g_tk:5381,
    uin:0,
    format:'jsonp',
    notice:0,
    platform:'h5page',
    needNewCode:1,
    from:'h5',
  })
  return jsonp(url,data,options)
}


